.Head section {
    position: relative;
    height: 150vh;
    display: flex;
    flex-direction: column;
    overflow: hidden;
}

#bg {
    position: absolute;
    width: 100vw;
    height: 150vh;
    left: 0;
    bottom: 0;
    z-index: 1;
}

#moon {
    position: absolute;
    width: 1000px;
    top: 10vh;
    z-index: 2;
}

#mountain {
    position: absolute;
    width: 100vw;
    bottom: 0;
    z-index: 3;
    height: 150vh;
    left: 0;
}

#txt {
    align-self: center;
    position: absolute;
    font-size: 10vw;
    color: #a3c5d7;
    bottom: 60vh;
    z-index: 2;
    animation: shake1 2s infinite;
}

#txt::before {
    position: absolute;
    content: '给我打钱！';
    font-size: 10vw;
    color: #85b3cd;
    left: 0;
    transform: translate(5px, 5px);
    animation: shake2 2s infinite;
}

#txt::after {
    position: absolute;
    content: '给我打钱！';
    transform: translate(10px, 10px);
    font-size: 10vw;
    color: #5288ae;
    left: 0;
    animation: shake3 2s infinite;
}

#body {
    height: 6000px;
    width: 100%;
    background-color: #547f99;
    overflow: hidden;
}

@keyframes shake1 {
    from {
        transform: rotate(10deg) scale(80%);
    }
    25% {
        transform: rotate(0deg) scale(120%);
    }
    50% {
        transform: rotate(-10deg) scale(80%);
    }
    25% {
        transform: rotate(0deg) scale(120%);
    }
    to {
        transform: rotate(10deg) scale(80%);
    }
}

@keyframes shake2 {
    from {
        transform: rotate(2deg);
    }
    50% {
        transform: rotate(-2deg);
    }
    to {
        transform: rotate(2deg);
    }
}

@keyframes shake3 {
    from {
        transform: rotate(5deg);
    }
    50% {
        transform: rotate(-5deg);
    }
    to {
        transform: rotate(5deg);
    }
}